    onPrint=()=>{

        let allData = this.props.table.getAllTableData(tableId);

        if(allData.rows.length === 0){
            toast({content:this.state.json['10140RATEG-000007'],color:'warning'});/*  */
            return;
        }

        let pks = [];

        allData.rows.forEach((item,key)=>{
            pks.push(item.values[pk_item].value);
        });
        print(
            'pdf',
            printUrl,
            {
				appcode:this.props.printFunCode,
                funcode:this.props.printFunCode,//
                nodekey:this.props.printNodeKey,//
                oids:pks
            }
        )
    }
    //
    onOutput=()=>{

        let allData = this.props.table.getAllTableData(tableId);
        if(allData.rows.length === 0){
            toast({content:this.state.json['10140RATEG-000008'],color:'warning'});/*  */
            return;
        }
        let pks = [];

        allData.rows.forEach((item,key)=>{
            pks.push(item.values[pk_item].value);
        });
        this.setState({
            ids : pks
        },this.refs.printOutput.open());
    }

